/* Language Switcher Component Styles */

.languageSwitcher {
  position: fixed;
  bottom: 30px;
  left: 25%;
  transform: translateX(-50%);
  z-index: 1000;
  display: flex;
  gap: 10px;
  background: white;
  padding: 12px 20px;
  border-radius: 30px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.15);
  border: 2px solid #2E3192;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
}

.languageSwitcher:hover {
  box-shadow: 0 6px 25px rgba(0,0,0,0.2);
  transform: translateX(-50%) translateY(-2px);
}

.languageBtn {
  padding: 8px 16px;
  border: 2px solid #e0e0e0;
  background: white;
  color: #555;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 5px;
  outline: none;
}

.languageBtn:hover {
  border-color: #2E3192;
  color: #2E3192;
}

.languageBtn:focus {
  outline: 2px solid #2E3192;
  outline-offset: 2px;
}

.languageBtn.active {
  background: linear-gradient(135deg, #2E3192 0%, #5158bb 100%);
  border-color: #2E3192;
  color: white;
}

.flag {
  width: 20px;
  height: 15px;
  border-radius: 2px;
  display: inline-block;
}

.flagUs {
  background: linear-gradient(to bottom, #002868 0%, #002868 33%, #ffffff 33%, #ffffff 66%, #bf0a30 66%);
}

.flagEs {
  background: linear-gradient(to bottom, #c60b1e 0%, #c60b1e 33%, #ffc400 33%, #ffc400 66%, #c60b1e 66%);
}

/* Mobile Responsive Design */
@media (max-width: 768px) {
  .languageSwitcher {
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 15px;
  }
  
  .languageSwitcher:hover {
    transform: translateX(-50%) translateY(-2px);
  }

  .languageBtn {
    padding: 6px 12px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .languageSwitcher {
    bottom: 15px;
    padding: 8px 12px;
  }

  .languageBtn {
    padding: 5px 10px;
    font-size: 11px;
    gap: 3px;
  }

  .flag {
    width: 16px;
    height: 12px;
  }
}